"use client";
import axios from "axios";
import { useParams } from "next/navigation";
import { useState, useEffect } from "react";
import { Form, Button, Container, Row, Col } from "react-bootstrap";

export default function page() {
  const { id } = useParams(); // Correct way to use params

  const [formData, setFormData] = useState({
    name: "",
    age: "",
    email: "",
  });

  useEffect(() => {
    if (!id) return; // Prevent API call if ID is undefined

    axios
      .get(`http://localhost:8080/updateuser/${id}`)
      .then((res) => {
        console.log("User Data:", res.data);
        setFormData(res.data); // Set form data with user details
      })
      .catch((err) => console.log("Error fetching user:", err));
  }, [id]); // Dependency array includes `id`

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    axios
      .put(`http://localhost:8080/users/${id}`, formData)
      .then((res) => {
        console.log("Updated User Data:", res.data);
        alert("User Updated Successfully!");
      })
      .catch((err) => console.log("Error updating user:", err));
  };

  return (
    <Container className="mt-5">
      <Row className="justify-content-md-center">
        <Col md={6}>
          <h2 className="text-center mb-4">Update User</h2>
          <Form onSubmit={handleSubmit} className="p-4 border rounded shadow">
            <Form.Group className="mb-3">
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter name"
                name="name"
                value={formData.name}
                onChange={handleChange}
                required
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>Age</Form.Label>
              <Form.Control
                type="number"
                placeholder="Enter age"
                name="age"
                value={formData.age}
                onChange={handleChange}
                required
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>Email</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                required
              />
            </Form.Group>

            <Button variant="warning" type="submit" className="w-100">
              Update User
            </Button>
          </Form>
        </Col>
      </Row>
    </Container>
  );
}
